export function UserQuit() {
    let nav = useNav();
    let note = <>
        <br />
        
    </>;
    return <Quit note={note}>
        <Button1 onClick={() => nav.close()} />
        <Button2 onClick={() => nav.open(<QuitConfirm />)} caption="" />
    </Quit>
}

function QuitConfirm() {
    let nav = useNav();
    let uqApp = useUqAppBase();
    let note = <>
        {waitingTime}
        {waitingTime}<br />
        
    </>;
    let onClickButton2 = async () => {
        await uqApp.userApi.userQuit();
        //let centerAppApi = new CenterAppApi(this.controller.net, 'tv/');
        //await centerAppApi.userQuit();
        nav.open(<QuitDone />);
    }
    return <Quit note={note}>
        <Button1 onClick={() => nav.close(2)} />
        <Button2 onClick={onClickButton2} caption="" />
    </Quit>
}

function QuitDone() {
    let uqApp = useUqAppBase();
    let note = <>
        {waitingTime}<br />
        {waitingTime}
        {waitingTime}
    </>;
    let onClickButton1 = () => {
        uqApp.logined(undefined);
    }
    return <Quit header="" note={note} back="none">
        <Button1 onClick={onClickButton1} caption="" />
    </Quit>
}
