import logo from '../../logo.svg';
import '../../App.css';
import { TestView } from "./TestView";
import { useEffect } from "react";
import { proxy } from 'valtio';
import { FA, Page } from 'tonwa-com';
import packageConfig from '../../../package.json';

/*
interface Props {
    tick: Tick;
}
*/

interface Item {
    id: number;
    name: string;
}

export interface Tick {
    count: number;
    text: string;
    list: Item[];
}

const tick = proxy<Tick>({
    count: 0,
    text: 'ok',
    list: [],
});

export function TabHome() {
    useEffect(() => {
        let timer = setInterval(() => {
            ++tick.count;
            let item = tick.list[3];
            if (item) {
                if (item.name.length > 100) item.name = '';
                item.name += ' - ' + Date.now();
            }
        }, 2000);
        return () => {
            clearInterval(timer);
        }
    }, []);

    let items: string[] = [
        ' app',
        ' uq ',
        'uq  app',
        'uq ',
        ' uq ',
        ' uq ',
        ' src/uqconfig.json  uqs',
        ' vs code Build UqAppuq typescript interface',
        ' uq ',
    ];
    return <Page header="">
        <div className="App">
            <header className="App-header">
                <img src={logo} className="App-logo" alt="logo" />
                <p>
                    <span className="text-success">tonwa</span> + <span className="text-primary">uq</span> = UI + DB
                </p>
                <div className="mb-3 h6 text-warning">
                    
                    <FA name="handshake-o" size="lg" className="text-danger mx-2" />
                    
                </div>
            </header>
            <div className="mb-3">{packageConfig.version}</div>
            <TestView tick={tick} />
            <ul className="text-start my-3 me-3">
                {items.map((v, index) => <li key={index} className="my-2">{v}</li>)}
            </ul>
        </div>
    </Page>;
}
